      getCheckedCityNum(city,format=0)
      {
          var num = 0;
          if(city)
          {
              
              for(var i=0;i<city.length;i++)
              {
                  if(city[i].checked)
                  {
                      num++;
                  }
              }

              if(format)
              return "("+num+")";
              else 
              return num;
          }
          
        return '';
      },
      updateCheckedCity(p)
      {
          if(p.city)
          {
              console.log(this.getCheckedCityNum(p.city), p.city.length)
              if(this.getCheckedCityNum(p.city) != p.city.length)
              p.checked = false;
              else
              p.checked = true;
          }

      }

    },
    mounted() {


    }
  }
</script>

