<template>
    <div>
        <h1>创建地域模板</h1>

        <div>
            <SetPostFreeArea ref="areaComponnet"></SetPostFreeArea>

        </div>

        <div>
            <el-form :model="tplForm" :rules="rules" ref="tplForm">
                <el-form-item prop="name">模板名称：<el-input v-model="tplForm.name"></el-input>
                </el-form-item>
                <el-button @click="submitForm('tplForm')" type="primary" :loading="tplForm.submitBtnLoading">立即保存</el-button>
                <el-button @click="resetForm('tplForm')">重置</el-button>
            </el-form>
        </div>

        <!--    {{param}} -->
    </div>
</template>

<script>
    import {
        request
    } from "@/network/request.js";
    import SetPostFreeArea from "@/components/commen/SetPostFreeArea.vue";
    import Qs from 'qs';


    export default {
        data() {
            return {
                area_ids: [],
                param: [],
                tplForm: {
                    id: 0,
                    name: "",
                    submitBtnLoading: false,

                },
                rules: {
                    name: [{
                            required: true,
                            message: '请输入模板名称',
                            trigger: 'blur'
                        },
                        {
                            min: 2,
                            max: 15,
                            message: '长度在 2 到 15 个字符',
                            trigger: 'blur'
                        }
                    ],
                }

            }
        },
        components: {
            SetPostFreeArea: SetPostFreeArea,
        },
        watch: {
            '$route'(to, from) {
                this.loadAreaTpl();
                

            }
        },
        methods: {

            submitForm(formName) {
                //console.log(this.$refs.areaComponnet.cityOptionsArea);

                this.tplForm.submitBtnLoading = true;


                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        console.log(valid);

                        let data = {
                            area_data: this.$refs.areaComponnet.cityOptionsArea,
                            name: this.tplForm.name,
                            id: this.tplForm.id
                        }

                        request({
                            url: "/subway/SaveAreaTpl",
                            method: 'post',
                            data: Qs.stringify(data),

                        }).then(res => {

                            console.log(res)

                            this.$message({
                                showClose: true,
                                message: '保存成功',
                                type: 'success'
                            });


                            this.$router.push({
                                path: "/bat/areaApply",
                            })
                            
                            this.tplForm.submitBtnLoading = false;


                        })

                    } else {
                        return false;
                    }


                });

                
            },
            resetForm(formName) {
                this.loadAreaTpl();
                this.$refs[formName].resetFields();
            },
            loadAreaTpl() {
                //console.log('again.')
                if (this.$route.query.id) {
                    request({
                        url: "/subway/GetAreaTpl",
                        method: 'get',
                        params: {
                            id: this.$route.query.id
                        },

                    }).then(res => {

                        console.log(res)

                        this.$refs.areaComponnet.cityOptionsArea = JSON.parse(res.data.source_data);
                        this.tplForm.id = this.$route.query.id;
                        this.$set(this.tplForm, 'name', res.data.tpl_name)



                    })
                } else {
                    this.$refs.areaComponnet.cityOptionsArea = this.$refs.areaComponnet.OptionsArea

                    this.tplForm.id = 0;
                    this.tplForm.name = '';
                }

            }

        },
        mounted() {
            this.loadAreaTpl();

        }
    }
</script>

